.expense-date{
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid white;
    background-color:#2a2a2a;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
}
.expense-date-month{
    font-size: 0.75rem;
    font-weight: bold; 
}
.expense-date-year{
    font-size: 0.75rem;
    font-weight: bold; 
}
.expense-date-day{
    font-size: 1.75rem;
    font-weight: bold; 
}