.expense-item{
    display: flex;
    justify-content: center;
    align-items: center;
    /* box-shadow: 0 2px 8px rgba(0,0,0,0.25); */
    padding: 0.5rem;
    margin: 1rem 0;
    /* border-radius: 12px; */
    background-color: #4b4b4b;
}

.expense-item-description{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
}

.expense-item h2{
    color: #3a3a3a;
    font-size:1rem;
    flex: 1;
    margin: 0rem;
    color: white;
    margin-left: 2rem;
}

.expense-item-price{
    font-size: 1rem;
    font-weight:bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius:12px;
}

@media(min-width: 580px){
    .expense-item-description{
        flex-direction: row;
        align-items: center;
        justify-content: center;
        flex: 1;    
    }

    .expense-item-description h2{
        font-size: 1.25rem;    
    }

    .expense-item-price {
        font-size: 1.25rem;
    }
}